/* NotFound.css */

.not-found {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
}

.not-found h1 {
    font-size: 48px;
}

.not-found a {
    color: #007bff;
    text-decoration: none;
}